import ReactDOM from "react-dom";
import { App } from "./app";
var d3 = require('d3');

var Prerender = require('d3-pre');
var prerender = Prerender(d3);



async function main() {
  prerender.start();
  const root = ReactDOM.createRoot(document.getElementById('root'));
  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
}